<template>
	<div class="g-ct">
		<div class="m-withdraw s-wh">
			<div class="m-form">
				<label for=""></label>
				{{user_info.username}}
			</div>
			<div class="m-form">
				<label for=""></label>
				<span class="busi-orange">{{user_info.useraccount}}</span>
			</div>
			<div class="m-form">
				<label for=""></label>
				<input type="number" :disabled="!parseFloat(user_info.useraccount)" placeholder="" step="0.01" v-model="withdrawNum" @keyup="updateValue">
			</div>
			<div class="m-form">
				<label for=""></label>
				<input type="password" placeholder="" v-model="password">
			</div>
		</div>
		<button class="u-submit-btn busi" :class="withdrawNum>=10&&password?'':'disabled'" @click="withdraw"></button>
		<div class="m-explain">
			<h3>:</h3>
			<p>1.</p>
			<p>2.1010</p>
			<p>3.</p>
			<p>4.</p>
			<p>5.7</p>
		</div>
	</div>	
</template>
<script>
	export default {
  		data () {
    		return {
    			user_info:{
    				username:'',
    				useraccount:'',
    				token:''
    			},
      			withdrawNum:'',
      			password:''
    		}
  		},
  		mounted:function(){
  			this.user_info.username = this.$route.query.username?this.$route.query.username:'';
  			this.user_info.useraccount = this.$route.query.useraccount?this.$route.query.useraccount:0;
  			this.user_info.token = this.$route.query.token?this.$route.query.token:'';
  		},
  		methods:{
  			// 
  			updateValue: function () {
			    this.withdrawNum = this.withdrawNum.toString()
				// 
		        .trim().replace(/[^\d.]/g,"")
		        //  2 
		        .slice(
		          0,
		          this.withdrawNum.toString().indexOf('.') === -1
		            ? this.withdrawNum.length
		            : this.withdrawNum.toString().indexOf('.') + 3
		        );
		        if(this.withdrawNum.toString().indexOf(".")< 0 && this.withdrawNum !=""){
			    // 0102 
			    	if(this.withdrawNum.toString()>100000){
			    		this.withdrawNum = 100000;
			    	}
			        this.withdrawNum= parseFloat(this.withdrawNum) + ''; 
			    }
			    if(!this.withdrawNum.toString()){
			    	this.withdrawNum = " ";
			    }
		    },
		    // 
		    withdraw:function(){
		    	this.$http.post('/api/userAccount/withdrawals',{token:this.user_info.token,money:this.withdrawNum,payPwd:this.password}).then(function(response) {
		    		if(response.data.code==200){
		    			this.$toast.show({
				    		text:'',
				    		position:'middle'
				    	})
		    		}else{
		    			this.$toast.show({
				    		text:response.data.msg,
				    		position:'middle'
				    	})
		    		}               	
	            }, function(response) {
	                this.$toast.show({
			    		text:'',
			    		position:'middle'
			    	})
	            });
		    }
  		}
	}
</script>
<style scoped lang="less">
	.m-withdraw{
		.m-form{
			height: 1.333333rem;
			line-height: 1.333333rem;
			font-size: 0.426667rem;
			color: #333;
			border-bottom: 0.013333rem solid #e6e6e6;
			label{
				display: inline-block;
				width: 2.4rem;
				padding-left: 0.4rem;
			}
		}
	}
	.disabled{
		background: rgba(72,162,221,0.5);
	}
</style>


