var Alert = require('./toast.vue') // vue
var Toast = {} // 
Toast.install = function(Vue, options) { // vueinstallvue
    // toast
    if (document.getElementsByClassName('alertBox').length) {
        return
    }
    let toastTpl = Vue.extend(Alert) // vue
    // elDOMVuecssHTMLElement
    // $vm.$el
    // vm.$mount()()
    // vuedom(html, body)
    // let $vm = new toastTpl({
    //  el: document.createElement('div')
    // })
    let $vm = new toastTpl() // vue
    // $mount$elbody
    let tpl = $vm.$mount().$el 
    document.body.appendChild(tpl)

    Vue.prototype.$toast = { // Vue
        show(options) { // toast
            if (typeof options === 'string') { // 
                $vm.text = options // props
            } else if (typeof options === 'object') {
                Object.assign($vm, options) // 
            }
            $vm.show = true // toast
        },
        hide() { // toast
            $vm.show = false
        }
    }
}
export default Toast; // Toast   


