<template>
	<div class="g-ct">
		<div class="g-vip">
			<div class="m-leaguer-top m-top" v-if="isLeaguer">
				<p>{{endtime}}</p>
			</div>
			<div class="m-common-top m-top" v-else></div>
			<div class="m-rule">
				<h3>{{isLeaguer?'':''}}</h3>
				<div class="rules">
					<p>1.9066.66/2000/</p>
					<p>2.2017810 11:00:00302000911200091111911</p>
				</div>
			</div>
			<div class="m-btns" v-if="isLeaguer">
				<button class="u-vip-btn renew" @click="showPay"></button>
			</div>
			<div class="m-btns" v-else>
				<button class="u-vip-btn" @click="showPay(0)">  2000</button>
				<button class="u-vip-btn" @click="showPay(1)">  6000</button>
			</div>
		</div>
		<div class="m-pop" v-if="show" @click="hidePay">
			<div class="pay-window leaguer-renew" v-if="isLeaguer>0">
				<ul>
					<li class="choose-month"><span id="targetInputTime">{{selectMonth}}</span></li>
					<li><span>2000/</span></li>
					<li><span>{{2000*month}}</span></li>
				</ul>
				<button class="u-vip-btn" @click="orderLeaguer"></button>
				<p></p>	
			</div>	
			<div class="pay-window" v-else>
				<ul>
					<li><span>2000/</span></li>
					<li><span>{{selectMonth}}</span></li>
					<li><span>{{2000*month}}</span></li>
				</ul>
				<button class="u-vip-btn" @click="orderLeaguer"></button>
				<p></p>
			</div>		
		</div>
		<div id='targetContainer'></div>
	</div>	
</template>
<script>
	import MultiPicker from '../../assets/MultiPicker.js'
	export default {
  		data () {
    		return {
      			isLeaguer:1,
      			show:false,
      			month:1,
      			selectMonth:'(30)',
      			endtime:''
    		}
  		},
  		mounted:function(){
  			var _this = this;
  			this.isLeaguer = this.$route.query.isLeaguer==0?0:this.$route.query.isLeaguer;
  			this.endtime = this.$route.query.endtime?this.$route.query.endtime:'';
  			new MultiPicker({
	            "input":"targetInputTime",
	            "container":"targetContainer",
	            "jsonData":[
	              {
	                'id':1,
	                'value':'1(30)',
	              },
	              {
	                'id':2,
	                'value':'2(60)',
	              },
	              {
	                'id':3,
	                'value':'3(90)',
	              },
	            ],
	            "success":function(arr){
	            	if(arr[0].id=='1'){
	            		_this.selectMonth = '(30)';
	            		_this.month = 1;
	            	}else if(arr[0].id=='2'){
	            		_this.selectMonth = '(60)';
	            		_this.month = 2;
	            	}else{
	            		_this.selectMonth = '(90)';
	            		_this.month = 3;
	            	}
	            }
	        });
  		},
  		methods:{
  			// 
  			showPay:function(type){
  				this.show = true;
  				this.month = type==1? 3 : 1;
  				this.selectMonth = type==1? '(90)' : '(30)';
  			},
  			// 
  			hidePay:function(e){
  				if(e.target.className == 'm-pop'){
  					this.show = false;
  				} 				
  			},
  			// 
  			orderLeaguer:function(){
  				window.location.href = 'lotteryopen://order?month='+this.month+'&amount='+this.month*2000+'&level=1';
  			}
  		}
	}
</script>
<style scoped lang="less">
	@import '../../assets/MultiPicker.css';
	// 
	.m-top{
		height: 4.666667rem;
		position: relative;
		p{
			font-size: 0.346667rem;
			color:#fff;
			font-weight: bold;
			position: absolute;
			bottom: 0.266667rem;
			left: 1.533333rem;
		}
	}
	.m-common-top{
		background: url(../../assets/images/general_bg.png) no-repeat;
		background-size: cover;
	}
	.m-leaguer-top{
		background: url(../../assets/images/leaguer_bg.png) no-repeat;
		background-size: cover;
	}
	.m-rule{
		padding:0.426667rem 0.266667rem 0;
		font-size: 0.4rem;
		color:#666;
		// 
		h3{
			background: url(../../assets/images/leaguer_icon.png) no-repeat;
			background-size: 0.68rem 0.546667rem;
			font-size: 0.453333rem;
			color:#333;
			font-weight: bold;
			text-indent: 0.946667rem;
		}
		// 
		.rules{
			height: 5.666667rem;
			overflow: auto;
			margin-top: 0.426667rem;
			p{
				margin-bottom: 0.426667rem;
			}
		}
	}
	.m-btns{
		margin-top: 0.96rem;
		.renew{
			margin-top: 1.533333rem;
		}
	}
	// 
	.pay-window{
		position: absolute;
		bottom: 0;
		height: 6.4rem;
		width: 100%;
		background: #fff;
		padding:0.4rem 0.466667rem;
		box-sizing: border-box;
		li{
			line-height: 1.066667rem;
			font-size: 0.426667rem;
			color:#666;
			span{
				color:#f75819;
				float:right;
			}
		}
		.u-vip-btn{
			width: 9.066667rem;
			margin:0.48rem auto 0.266667rem;
		}
		p{
			font-size: 0.32rem;
			color:#999;
			text-align: center;
		}
	}
	// 
	.leaguer-renew{
		height: 6.933333rem;
		padding:0;
		ul{
			background: #f0f3f7;
			li{
				border-bottom: 0.013333rem solid #e6e6e6;
				line-height: 1.333333rem;
				padding:0 0.466667rem;
				background: #fff;
				&.choose-month{
					border-bottom: none;
					margin-bottom: 0.266667rem;
					span{
						padding-right: 0.48rem;
						color:#333;
						background: url(../../assets/images/arrow.png) right center no-repeat;
						background-size: 0.213333rem 0.426667rem;
					}
				}
			}
		}
	}
</style>


